body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App{
    width: 1280px;
    margin: 20px auto 0 auto;
}
.main{
  width: 500px;
  margin: 0 auto;
}

h1{
    text-align: center;
    color: #666;
    margin-top: 50px;
}

.title{
    display: flex;
    line-height: 32px;
    margin-bottom: 20px;
    margin-top: 30px;

}
.title>div:nth-child(1){
    width: 20px;
}

.title>div:nth-child(2){
    width: 70px;
}
.title>div:nth-child(3){
    flex: 1 1;
    /* margin-right: 10px; */
}


.item{
    display: flex;
    line-height: 32px;
    margin-bottom: 10px;
}
.item>div:nth-child(1){
    width: 70px;
    text-align: center;
}

.item>div:nth-child(2){
    /* width: 356px; */
    flex: 1 1;
}

button {
    margin: 0 10px;
}

.footer{
    text-align: center;

}
.footer button{
    margin: 0 10px;
}

.main-content{
    width: 500px;
    margin: 20px auto;
}
.item{
   margin-left: 20px;
}

h2{
    margin-top: 20px;
}
.ant-button {
    margin: 0 10px;
}
